/* Global */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

/* App.js */
.container {
  max-width: 1100px;
  margin: auto;
}

.btn {
  margin: 0.6rem;
  padding: 0.7rem 1.1rem;
  cursor: pointer;
  background-color: royalblue;
  border: none;
  border-radius: 3px;
  font-size: 1.1rem;
}

.btn:hover {
  opacity: 0.7;
}

.btn:active {
  transition: all 0.1s ease;
  transform: scale(1.1);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal .modal-content {
  padding: 1.2rem;
  background-color: #ebebeb;
  border-radius: 2px;
  animation: showModal 1s;
}

.modal .modal-content .modal-header {
  display: flex;
  align-items: center;
  padding-left: 0.4rem;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.modal .modal-content .modal-header h1{
  margin-right: 1rem;
  font-size: 1.6rem;
}

.modal .modal-content .modal-header i{
  font-size: 1.3rem;
  cursor: pointer;
}

.modal .modal-content .modal-header i:hover{
  transform: scale(1.05);
}

.modal .modal-content .modal-buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}

.modal .modal-content .modal-buttons button{
  padding: 3rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
}

.modal .modal-content .modal-buttons button:hover{
  transition: all 0.1s ease;
  transform: scale(1.1);
}

.modal .modal-content .modal-buttons button:first-child{
  background-color: crimson;
}

.modal .modal-content .modal-buttons button:last-child{
  background-color: steelblue;
}

/* Animations */
@keyframes showModal {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}
